.container {
  display: flex;
  flex-direction: column;
}
.container header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 10%;
  margin-top: 5%;
}
.container header .c1 h2{
  color: #61B846;
  font-family: sans-serif;
  font-weight: bold;
}
.container header .c1 h6{
  color: #024F9D;
  font-family: monospace;
  font-weight: bold;
  font-size: 4vh;
}
.container header .c1 h2,h6{
  margin: 0;
}
.container header h6 {
  color:gray
}
.container .c1-logo img{
  width: 4vw;
}
.container .main {
  margin: 0 10%;
  margin-top: 2%;

}
.container .main .grocery{
  width: 100%;
  margin-bottom: 3%;
}
.container .main .grocery img{
  width: 100%;
}
/* search w3school */
form.example input[type=text] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  border-right: none; /* Prevent double borders */
  background: #f1f1f1;
  border-radius: 50px 0px 0px 50px;
}
/* Style the submit button */
form.example button {
  float: left;
  width: 20%;
  padding: 10px;
  background: #f1f1f1;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
  border-radius: 0px 50px 50px 0px;

}
form.example input:focus {
  outline: none;
}
form.example button:focus {
  outline: none;
}
/* Clear floats */
form.example::after {
  content: "";
  clear: both;
  display: table;
}
.main .example .fa {
  color: black;
}

/* shoy by Categery */

.slides {
  margin: 0 10%;
  margin-top: 2%;
  margin-bottom: 5%;
}
.cards {
  display: flex;
  overflow-x: scroll;
  gap:1rem
}
::-webkit-scrollbar{
  display: none;
}
.slides .cards img {
  width: 15vw;
  height: 20vh;
}

.slides .cards p {
  text-align: center;
}

.categery {
  margin: 0 10%;
  display: flex;
  flex-direction: column;
  height: 70vh;
  overflow-y: scroll;
}
.categery .cg-1 {
  display: flex;
  align-items: center;
}
.categery .content {
  width: 100%;
}
.categery .cont1 , .cont2 {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.categery img {
  width: 20vw;
}
.categery .cont2 button {
  width: 6vw;
  height: 6vw;
  border-radius: 15%;
  background-color: #61B846;
  border: 2px solid green;
}
.cg-1 h2 , button {
  font-size: 3vw;
}